.portfolio-items {
  padding: 5% 5%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 5%;
}
.port-title {
  margin: 0;
  padding-bottom: 1rem;
  padding-top: 0;
}

.port-subtopic-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 0rem 1rem 0rem;
}
